    src:url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    font-family:"RestaurantFont";
    font-weight:normal;
    font-style: normal;
}
@font-face {
    src:url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    font-family:"RestaurantHeaderFont";
    font-weight:normal;
    font-style: normal;
}
*{
    font-family:RestaurantFont,Arial, Helvetica, sans-serif;
    --text-color:#f5f5f5;
    --text-color2:#a5a5a5;
}
body,html{
    padding:0;
    margin:0;
}
#content{
    background-size: cover;
    background-position: center;
    position: fixed; 
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1; 
    display:grid;
    grid-template-rows: auto 1fr 200px;
    justify-content: center;
}

#content::before{
    content:"";
    width:100%;
    height:100%;
    background-color: #00000066;
    position: absolute;
    top:0;
    z-index:0;

}
/* header */
header{
    padding:1rem 0;
    background-color: #000000cc;
    text-align: center;
}
header h1{
    font-size:70px;
    font-family:RestaurantHeaderFont,Arial, Helvetica, sans-serif;
    color:var(--text-color);
    font-weight: 300;
    margin:15px;
}
header nav{
    display:flex;
    justify-content: center;
    background-color:#766a6a22;
    padding:15px 0;
}
header nav a{
    color:var(--text-color);
    /* margin:0 25px; */
    font-size:25px;
    padding:10px 25px;
    text-align: center;
    cursor:pointer;
}

header nav a.active{ 
    border-bottom:1px solid var(--text-color);
}

/* footer */
footer{
    background-color: #000000cc;
    text-align: center;
    padding:20px;

}
footer p{
    color:var(--text-color);
}
/* main */
main {
    margin:50px;
    width:50%;
    text-align: center;
    margin-left:auto;
    margin-right:auto;
}
@media Screen and (max-width:991px){
    main{
        width:100%;
    }
}
/*  homepage */
main .homeDiv{
    display: flex;
    flex-direction: column;
    background-color: #00000066;
    padding:50px;

    
}
main .homeDiv div{
    color:var(--text-color);
    text-align: center;
    background-color: #00000055;
    padding:50px;
    margin-bottom:20px;
}
